.sidebar {
  height: 75px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  position: fixed;
  top: 0;
  background-color: white;
  z-index: 3;
  background: transparent;
  backdrop-filter: blur(5px);
}

ul {
  list-style-type: none;
  padding-left: 0;
}
/* Heading/Heading 4 - 18px */
.welcomeMsg {
  font-weight: 800;
font-size: 24px;
line-height: 33px;

/* identical to box height */
display: flex;
align-items: center;
letter-spacing: -0.03em;

color: #1C37C5;
  margin-left: 13px;
  
  font-size: 24px;
  line-height: 25px;
  padding: 8px 0px 8px 16px;
  font-weight: bold;
  
  text-transform: uppercase;
}

/* identical to box height */
.menu-bars {
  margin-left: 20px;
  font-size: 2rem;
  background: none;
  margin-top: 10px;
}

.nav-menu {
  background-color: white;
  width: 75%;
  min-height: 100%;

  display: flex;
  /* align-items: center; */
  position: absolute;
  top: 0;
  left: -100%;
  transition: 850ms;
  border: inset black 0.5px;
  z-index: 3;

}

.nav-menu.active {
  position: fixed;
  left: 0;
  transition: 350ms;
}

.nav-text {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
  letter-spacing: -0.03em;
  // font-variant: small-caps;
  z-index: 2;
  /* Neutral/Black */
  color: #000000;
}

.nav-text a {
  letter-spacing: -0.03em;
  // font-variant: small-caps;
  color: #000000;
  text-decoration: none;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}

.nav-text a:hover {
  background-color: #7dbadd;
}

.nav-menu-items {
  width: 100%;
}

.userIcon{
  height: 20px;
  width: 20px;
}

.navbar-toggle {
  background-color: white;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.help{
  margin-left: 16px;
}

.logo{
  height: 65px;
width: 150px;
margin-left: 60px;
// margin-top: 8px;
}

.notLoggedIn{
  padding: 10px;
  margin:10px;
  letter-spacing: -0.03em;
  font-variant: 'Avenir Next';
}

.log{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 8px 73px;
  gap: 8px;
  
  position: absolute;
  width: 240px;
  height: 48px;
  
  /* Brand/Blue */
  background: #1C37C5;
  border-radius: 8px;
  color:white
}

.sidebar-text{
  padding: 0 15px;
}

.backIcon{
  margin-left: 0;
  font-size: 2rem;
  background: none;
  margin-top: 15px;
}

.loginPls{
  margin-left: 5px;
}

.logoNameMenu{
  font-family: 'Saira Condensed';
font-style: normal;
font-weight: 700;
font-size: 35px;
line-height: 65px;

/* identical to box height */


color: #9C1C8F;

