.homepage{
  width: 100%;
  display: flex;
  height: 91vh;
  align-items: center;
  justify-content: center;
  position: absolute;
top: 0;
z-index: -1;
}

.logoImg {
  height: 65px;
  top: 14px;
  width: 150px;
  
  position: absolute;
  display: flex;
  
}

.logobar {
  position: fixed;
  display: flex;
  height: 80px;
  width: 100%;
  justify-content: center;
  // background: transparent;
  backdrop-filter: blur(3px);
  background: rgba(255, 255, 255, 0.5);
}

footer {
  position: absolute;
  display: flex;
  justify-content: center;
}

.shoppingCart{
  display: flex;
position: absolute;
right: 20px;
top: 32px;
height: 30px;
width: 30px;
}

.logoName{
  font-family: 'Saira Condensed';
font-style: normal;
font-weight: 700;
font-size: 38px;
line-height: 65px;

/* identical to box height */

margin-top: 15px;
color: #9C1C8F;

}
.vrHome{
  font-family: 'Saira Condensed';
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 25px;
  
  
  color: #9C1C8F;
  opacity: 100;
  background-color: none;
  
}